// Override default accordion body rule
.js
    .accordion
    > .accordion__heading[data-open='true']
    + .accordion__body
    > .timeline__content.expanded {
    top: $measure--quarter;
    @include media('>=small') {
        top: $measure--small5;
        margin-bottom: $measure--medium4;
    }
}

.timeline__content {
    position: relative;
    &.expanded {
        &::before {
            /* Vertical line */
            content: '';
            position: absolute;
            left: 18px;
            height: 100%;
            width: 4px;
            background-color: $color-nearblack;
            z-index: z-index('default');
            @include media('>=small') {
                left: 50%;
            }
        }
    }
}

.timeline__content__block {
    width: 100%;
    padding-bottom: $measure--basic;
    position: relative;
    &:nth-child(1) {
        padding-top: $measure--basic;
    }
    &:first-of-type {
        top: $measure--quarter;
        @include media('>=small') {
            .timeline__content__block__item__connector {
                top: $measure--small5;
            }
        }
    }
    @include media('>=small') {
        margin-top: -5%;

        &:nth-child(even) .timeline__content__block__item {
            position: relative;
            left: 55%;
        }
        &:nth-child(even) .timeline__content__block__item__connector {
            left: -11%;
            span {
                left: 80px;
            }
        }
    }
}

.timeline__content__block__item {
    border: 1px solid #ccc;
    padding: $measure--basic;
    margin: 0 20px 0 45px;
    background-color: $color-purewhite;

    @include media('>=small') {
        margin: 0 45px 0 0;
        width: 45%;
    }

    h2 {
        font-weight: bold;
        font-size: 1.49em;
    }

    .timeline__content__block__item__text {
        &:last-child {
            margin-bottom: 0;
        }
        img {
            display: block;
            margin-bottom: $measure--basic;
        }
    }
}

.timeline__content__block__item__connector {
    border-top: solid 2px black;
    position: relative;
    left: -42px;
    width: 50px;
    height: 1px;

    @include media('>=tablet') {
        border-top: solid 2px black;
    }
    @include media('>=small') {
        position: absolute;
        width: 80px;
        left: auto;
        right: 50%;
    }

    span {
        width: 7px;
        height: 7px;
        border: solid 1px black;
        border-radius: 50%;
        display: block;
        color: transparent;
        position: relative;
        top: -4px;
        left: 50px;

        @include media('>=tablet') {
            width: 10px;
            height: 10px;
            border: solid 2px black;
            top: -6px;
            left: 50px;
        }
        @include media('>=small') {
            left: auto;
            right: 10px;
        }
    }
}

.timeline {
    @include media('>=small') {
        .timeline__content__block:nth-child(even) {
            animation: timeline-bounce-2 0.6s;
        }
        .timeline__content__block:nth-child(odd) {
            animation: timeline-bounce-2-inverse 0.6s;
        }
    }
}
@keyframes timeline-bounce-2 {
    0% {
        opacity: 0;
        transform: translateX(-100px);
    }

    60% {
        opacity: 1;
        transform: translateX(20px);
    }

    100% {
        transform: translateX(0);
    }
}

@keyframes timeline-bounce-2-inverse {
    0% {
        opacity: 0;
        transform: translateX(100px);
    }

    60% {
        opacity: 1;
        transform: translateX(-20px);
    }

    100% {
        transform: translateX(0);
    }
}

.no-js .timeline {
    .collapsed {
        display: block;
    }
}
